<template>
  <div>

    <video :src="filePath" controls="controls" class="video" preload>
      
    </video>

  </div>
</template>

<script>

export default {
  components: {

  },
  props: ['filePath'],

  mounted () {
    this.watchVideoState()
  },
  methods: {
    watchVideoState () {
      const _this = this
      const $video = document.getElementsByTagName('video')[0]// DOM

      $video.addEventListener('playing', function () { // Doing
        _this.playState = true// 
        console.log('')
        _this.$emit('startStudy')
      }); $video.addEventListener('pause', function () { // Doing
        _this.playState = false// 
        console.log('')
        _this.$emit('pauseStudy')
      })
    }
  },
  beforeDestroy () {

  }

}

</script>

