  <div>

    <audio :src="filePath" controls="controls" class="audio" preload>
      
    </audio>

  </div>
</template>

<script>

export default {
  components: {

  },
  props: ['filePath'],

  mounted () {
    this.watchAudioState()
  },
  methods: {
    watchAudioState () {
      const _this = this
      const $audio = document.getElementsByTagName('audio')[0]// DOM

      $audio.addEventListener('playing', function () { // Doing
        _this.playState = true// 
        console.log('')
        _this.$emit('startStudy')
      }); $audio.addEventListener('pause', function () { // Doing
        _this.playState = false// 
        console.log('')
        _this.$emit('pauseStudy')
      })
    }
  },
  beforeDestroy () {

  }

}

</script>

